/* Start:: Jobs Candidate Details */
.list-bullets li {
  position: relative;
  margin-block-end: -1px;
  border: 1px solid $default-border;
  list-style-type: circle;
  list-style-position: inside;
  padding: 0.75rem 1.25rem;
}
/* End:: Jobs Candidate Details */

/* Start:: CRM Contacts */
.crm-contact-close-btn.btn-close {
  filter: invert(1);
  position: absolute;
  inset-inline-end: -0.625rem;
  inset-block-start: -0.625rem;
}
/* End:: CRM Contacts */

/* Start:: CRM Companies */
#offcanvasExample {
  border-color: transparent !important;
}
/* End:: CRM Companies */

/* Start:: CRM Deals */
.lead-discovered,
.lead-qualified,
.contact-initiated,
.need-identified,
.negotiation,
.deal-finalized {
  position: relative;
  margin-inline-start: 1rem;
  &:before {
    position: absolute;
    content: "";
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 50%;
    inset-inline-start: -0.875rem;
    inset-block-start: 0.4375rem;
  }
}
.lead-discovered:before {
  background-color: $primary;
}
.lead-qualified:before {
  background-color: $warning;
}
.contact-initiated:before {
  background-color: $success;
}
.need-identified:before {
  background-color: $info;
}
.negotiation:before {
  background-color: $danger;
}
.deal-finalized:before {
  background-color: $secondary;
}

#leads-discovered .card.custom-card {
  border-inline-start: 0.4rem solid rgba(var(--primary-rgb), 0.4) !important;
  .company-name {
    color: $primary;
  }
  .avatar {
    background-color: $primary;
  }
}
#leads-qualified .card.custom-card {
  border-inline-start: 0.4rem solid rgba(var(--warning-rgb), 0.4) !important;
  .company-name {
    color: $warning;
  }
  .avatar {
    background-color: $warning;
  }
}
#contact-initiated .card.custom-card {
  border-inline-start: 0.4rem solid rgba(var(--success-rgb), 0.4) !important;
  .company-name {
    color: $success;
  }
  .avatar {
    background-color: $success;
  }
}
#needs-identified .card.custom-card {
  border-inline-start: 0.4rem solid rgba(var(--info-rgb), 0.4) !important;
  .company-name {
    color: $info;
  }
  .avatar {
    background-color: $info;
  }
}
#negotiation .card.custom-card {
  border-inline-start: 0.4rem solid rgba(var(--danger-rgb), 0.4) !important;
  .company-name {
    color: $danger;
  }
  .avatar {
    background-color: $danger;
  }
}
#deal-finalized .card.custom-card {
  border-inline-start: 0.4rem solid rgba(var(--secondary-rgb), 0.4) !important;
  .company-name {
    color: $secondary;
  }
  .avatar {
    background-color: $secondary;
  }
}
#leads-discovered,
#leads-qualified,
#contact-initiated,
#needs-identified,
#negotiation,
#deal-finalized {
  .card.custom-card {
    margin-block-end: 0.5rem;
    touch-action: none;
    &:last-child {
      margin-block-end: 1.5rem;
    }
    .card-body {
      padding: 1rem;
      .deal-description {
        margin-inline-start: 2.25rem;
      }
    }
  }
}
/* End:: CRM Deals */

/* Start:: Create NFT*/
.create-nft-item {
  .filepond--root[data-style-panel-layout~="circle"] {
    border-radius: $default-radius;
    width: 100%;
  }
  .filepond--drop-label.filepond--drop-label label {
    padding: 2em;
  }
  .filepond--drop-label {
    color: $text-muted !important;
  }
  .filepond--panel-root {
    border: 0.125rem dashed $input-border !important;
  }
  .filepond--root[data-style-panel-layout~="circle"]
    .filepond--image-preview-wrapper {
    border-radius: 0.3rem;
  }
}
.create-nft-item {
  .filepond--root[data-style-panel-layout~="circle"] {
    height: 15.75rem !important;
    width: 15.25rem !important;
  }
  .filepond--drop-label label {
    font-size: 0.75rem;
  }
  .filepond--root[data-style-panel-layout~="circle"]
    .filepond--image-preview-wrapper,
  .filepond--file {
    height: 15.75rem;
    width: 15.25rem !important;
  }
}
/* End:: Create NFT*/

/* Start:: NFT Details */
.nft-details-auction-time {
  position: absolute;
  width: 400px;
  height: 1.875rem;
  background-color: rgb(var(--success-rgb));
  display: flex;
  align-items: center;
  justify-content: center;
  color: $white;
  inset-block-start: 3.125rem;
  font-weight: 500;
  inset-inline-end: -8.4375rem;
  transform: rotate(45deg);
  z-index: 10;
}
[dir="rtl"] {
  .nft-details-auction-time {
    transform: rotate(315deg);
  }
}
/* End:: NFT Details */

/* Start:: NFT Wallet */
.nft-wallet {
  position: relative;
  text-align: center;
  box-shadow: none !important;
  border: 1px solid $default-border !important;
  border-radius: 0.5rem;
  &:hover {
    background-color: $light;
  }
  &.active {
    border-color: $primary !important;
  }
}
/* End:: NFT Wallet */

/* Start:: NFT Live Auction */
