  &.active {
    color: $secondary;
    .nft-tag-icon {
      background-color: rgba(var(--secondary-rgb), 0.1);
    }
  }
  .nft-tag-icon {
    color: $secondary;
  }
}
.nft-tag.nft-tag-warning {
  &:hover,
  &.active {
    color: $warning;
    .nft-tag-icon {
      background-color: rgba(var(--warning-rgb), 0.1);
    }
  }
  .nft-tag-icon {
    color: $warning;
  }
}
.nft-tag.nft-tag-info {
  &:hover,
  &.active {
    color: $info;
    .nft-tag-icon {
      background-color: rgba(var(--info-rgb), 0.1);
    }
  }
  .nft-tag-icon {
    color: $info;
  }
}
.nft-tag.nft-tag-success {
  &:hover,
  &.active {
    color: $success;
    .nft-tag-icon {
      background-color: rgba(var(--success-rgb), 0.1);
    }
  }
  .nft-tag-icon {
    color: $success;
  }
}
.nft-tag.nft-tag-danger {
  &:hover,
  &.active {
    color: $danger;
    .nft-tag-icon {
      background-color: rgba(var(--danger-rgb), 0.1);
    }
  }
  .nft-tag-icon {
    color: $danger;
  }
}
.nft-tag.nft-tag-dark {
  &:hover,
  &.active {
    color: $dark;
    .nft-tag-icon {
      background-color: rgba(var(--dark-rgb), 0.1);
    }
  }
  .nft-tag-icon {
    color: $dark;
  }
}
.nft-tag .nft-tag-text {
  font-weight: 600;
  display: inline-block;
  padding: 0px 1.25rem 0px 0.5rem;
}
.nft-tag .nft-tag-icon {
  display: inline-block;
  padding: 0.75rem 1rem;
  border-radius: 50%;
  background-color: $light;
}
.nft-tag {
  position: relative;
  display: inline-flex;
  align-items: center;
  border: 1px solid $custom-white;
  border-radius: 50rem;
  background-color: $custom-white;
  color: $default-text-color;
}
.nft-timer-container {
  position: absolute;
  inset-inline-start: 0;
  inset-block-start: 5%;
  display: inline-flex;
  align-items: center;
  padding: 0.2rem 0.7rem;
  border-start-start-radius: 0px;
  border-start-end-radius: 50rem;
  border-end-end-radius: 50rem;
  border-end-start-radius: 0px;
  color: $white;
  font-weight: 500;
  background: rgba(255, 255, 255, 0.1);
}
.nft-btn {
  position: absolute;
  inset-inline-end: 5%;
  inset-block-end: 5%;
  height: 2rem;
  width: 2rem;
  padding: 5px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.1);
  color: $white;
  font-size: 1rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all ease 0.3s;
  z-index: 1;
  &:hover {
    background-color: $custom-white;
    color: $primary;
  }
}
.nft-img {
  border-radius: 0.5rem;
  max-height: 10rem;
  width: 100%;
}
/* End:: NFT Live Auction */

/* Start:: Crypto Wallet */
[data-theme-mode="dark"] {
  .qr-image {
    filter: invert(1);
  }
}
/* End:: Crypto Wallet */

/* Start:: Crypto Currency Exchange */
#btc-currency-chart,
#eth-currency-chart,
#ltc-currency-chart,
#dash-currency-chart,
#xrs-currency-chart,
#glm-currency-chart,
#monero-currency-chart,
#eos-currency-chart {
  position: absolute;
  inset-block-end: 0;
}
.currency-exchange-card {
  position: relative;
  background-color: $primary !important;
  box-shadow: none;
  min-height: 21.875rem;
  z-index: 10;
  &:before {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    background-image: url(../images/media/media-1.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    z-index: 0;
    opacity: 0.1;
  }
  .currency-exchange-area {
    background-color: rgba(var(--dark-rgb), 0.1);
    backdrop-filter: blur(30px);
  }
}
/* End:: Crypto Currency Exchange */

/* Start:: Loader */
#loader {
  position: fixed;
  inset-block-start: 0;
  inset-inline-start: 0;
  width: 100%;
  height: 100%;
  background-color: $custom-white;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}
/* End:: Loader */

/* Start:: Offcanvas body padding*/
@media (min-width: 992px) {
  body {
    padding-inline-end: 0 !important;
    overflow: auto !important;
  }
}
/* end:: Offcanvas body padding*/

/* start:: kanban*/
.kanban-board {
  width: 370px;
}
[class^="ri-"],
[class*=" ri-"] {
  display: inline-flex;
}
/* end:: kanban*/

/* Start:: rtl arrow rotate */
[dir="rtl"] {
