/* End:: Bootstrap Updated Styles */

/* Settings Scss Start */
.settings-list {
  ul {
    margin-block-end: 0;
    border: 1px solid $default-border;
    border-radius: 8px;
    padding: 0;
    li {
      border-radius: 8px;

      &:hover {
        a i {
          color: $primary;
          fill: $primary;
        }
      }
    }
    a {
      padding-block: 8px;
      padding-inline: 7px;
      font-size: 0.875rem;
      font-weight: 400;

      i {
        color: $default-text-color;
        fill: $default-text-color;
      }
    }
  }
  .nav-link:focus {
    color: $white !important;
    background-color: $primary !important;
    i {
      color: $white !important;
    }
  }
}

.settings-main-icon {
  display: flex;
  i {
    width: 3rem;
    font-size: 18px;
    line-height: 3rem;
    height: 3rem;
    background-color: $primary-02;
    color: $primary;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

/* Profile Scss Start */

.card.custom-card .dash2 {
  border-radius: 26%;
}
.card.custom-card .dash2:before {
  content: "";
  position: absolute;
  width: 7px;
  height: 70px;
  inset-block-start: -20%;
  inset-inline-end: 30%;
  background: rgba(255, 255, 255, 0.135);
  transform: rotate(45deg);
}
.card.custom-card .dash2:after {
  content: "";
  position: absolute;
  width: 7px;
  height: 100px;
  inset-block-start: -83%;
  inset-inline-end: 30%;
  background: rgba(255, 255, 255, 0.135);
  transform: rotate(45deg);
}

.profile-biodata-link {
  a {
    color: $primary;
    &:hover {
      color: $default-text-color;
    }
  }
}

.profile-statistics {
  ul {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
      width: 100px;
      height: 70px;
      text-align: center;
      padding: 0.5rem;
      margin-block: 0.25rem;
      border-radius: 5px;
      border: 1px solid $default-border;
      background-color: $default-background;

      &:nth-child(2) {
        margin-inline: 0.5rem;
      }

      span {
        font-size: 21px;
      }
    }
  }
}

.profile-timeline-post {
  height: 200px;
}

@media (max-width: 576px) {
  .profile-timeline-post {
    height: 160px;
  }
}

.profile-friend {
  margin-block-start: 0.5rem;

  ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;

    li {
      padding: 7px;
      a {
        width: 30px;
        height: 30px;
        display: block;
        border: 1px solid $default-border;
        line-height: 25px;
        border-radius: 50%;
        background: $bootstrap-card-border;
        color: var(--primary-bg-color);
        transition: all ease 0.3s;
        line-height: 2;

        &:hover {
          background-color: $primary;
          i {
            color: $white !important;
          }
        }

        i {
          display: flex;
          align-items: center;
          justify-content: center;
          color: $primary;
          font-size: 15px;
        }
      }
    }
  }
}

.contact-card-star {
  position: absolute;
  inset-block-start: 8px;
  inset-inline-start: 15px;
}

.contact-card-options {
  position: absolute;
  inset-block-start: 10px;
  inset-inline-end: 15px;
}

.pricing-card {
  &:hover {
    box-shadow: 0px 16px 32px rgba(33, 33, 33, 0.15);
    transition: all ease 0.3s;
  }

  .checkmark-bg {
    padding: 3px 4px 3px 5px;
    font-weight: 500;
  }
  .checkmark {
    margin-block-end: 2px;
  }
}

.pricing-card.advanced {
  border: 1px solid $primary !important;

  &:hover {
    box-shadow: 0 7px 30px rgba(119, 188, 33, 0.2) !important;
  }
}

[dir="rtl"] {
  .dropdown-menu {
    --bs-position: start;
  }
  .dropdown-menu-end {
    --bs-position: end;
  }
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
