/* Header Colors Styles*/

//header-light start
[data-header-styles="light"] {
  --header-bg: #fff;
  --header-border-color: #f3f3f3;
  --header-prime-color: #536485;

  .header-link-icon:hover {
    background-color: none;
  }
  .header-search-icon {
    fill: #536485;
    color: #536485;
  }
  .header-search {
    .form-control::-webkit-input-placeholder {
      --placeholder-text-color: #536485;
    }
  }
}

//header-light start

//header-dark start
[data-header-styles="dark"] {
  --header-bg: #{var(--custom-white)};
  --header-border-color: rgba(255, 255, 255, 0.1);
  --header-prime-color: rgba(255, 255, 255, 0.8);

  &[data-nav-layout="horizontal"] {
    @media (min-width: 992px) {
      .header-element .horizontal-logo .header-logo {
        .desktop-dark {
          display: block;
        }

        .desktop-logo,
        .toggle-dark,
        .toggle-logo,
        .desktop-white,
        .toggle-white {
          display: none;
        }
      }
    }
  }

  .app-sidebar .slide__category {
    opacity: 1;
  }

  .main-header-container {
    .header-link:hover,
    .header-link.show {
      .header-link-icon {
        fill: $primary;
      }
    }
  }
  .header-search-icon {
    fill: #536485;
    color: #536485;
  }
  &[data-theme-mode="dark"] {
    .header-search-icon {
      fill: #fff;
      color: #fff;
    }
  }

  &[data-theme-mode="light"] {
    --header-bg: #011626;
  }
}

//header-dark start

//header-color start
[data-header-styles="color"] {
  --header-bg: #{$primary};
  --header-border-color: rgba(255, 255, 255, 0.1);
  --header-prime-color: rgba(255, 255, 255, 0.6);

  &[data-nav-layout="horizontal"] {
    @media (min-width: 992px) {
      .header-element .horizontal-logo .header-logo {
        .desktop-white {
          display: block;
        }

        .desktop-logo,
        .desktop-dark,
        .toggle-dark,
        .toggle-white,
        .toggle-logo {
          display: none;
        }
      }
    }
  }

  .main-header-container {
    .header-link:hover,
    .header-link.show {
      .header-link-icon {
        fill: $white;
        color: $white;
      }
      .profile-heading {
        color: $white;
      }
    }

    .header-link-icon:hover {
      background-color: transparent;
      color: $white;
    }
    .sidemenu-toggle:hover {
      color: $white;
    }
  }
  .header-search-icon {
    fill: #536485;
    color: #536485;
  }
  &[data-theme-mode="dark"] {
    .header-search-icon {
      fill: #fff;
      color: #fff;
    }
  }
}

//header-color start

//header-gradient start
[data-header-styles="gradient"] {
  --header-bg: linear-gradient(to right, #{$primary} 0%, #00b9da 100%);
  --header-border-color: rgba(255, 255, 255, 0.1);
  --header-prime-color: rgba(255, 255, 255, 0.6);

  .sidemenu-toggle:hover {
    color: $white;
  }
  .main-header-container {
    .header-link:hover {
      .profile-heading,
      .header-link-icon {
        color: $white;
        background: transparent;
      }
    }
  }

  &[data-nav-layout="horizontal"] {
    @media (min-width: 992px) {
      .header-element .horizontal-logo .header-logo {
        .desktop-white {
          display: block;
        }

        .desktop-logo,
        .desktop-dark,
        .toggle-dark,
        .toggle-white,
        .toggle-logo {
          display: none;
        }
      }
    }
  }

  .main-header-container {
    .header-link:hover,
    .header-link.show {
      .header-link-icon {
        fill: $white;
      }
    }

    .header-link-icon:hover {
      background-color: rgba(255, 255, 255, 0.1);
    }
  }
  .header-search-icon {
    fill: #536485;
    color: #536485;
  }
  &[data-theme-mode="dark"] {
    .header-search-icon {
      fill: #fff;
      color: #fff;
    }
  }
}

//header-gradient start

//header-transparent start
[data-header-styles="transparent"] {
  .main-header-container .header-link-icon:hover {
    background-color: rgba(0, 0, 0, 0.05);
  }

  &[data-theme-mode="light"] {
    --header-bg: rgb(var(--body-bg-rgb));
    --header-border-color: rgba(0, 0, 0, 0.07);
  }

  &[data-theme-mode="dark"] {
    --header-bg: rgb(var(--body-bg-rgb2));
    --header-prime-color: rgba(255, 255, 255, 0.6);
    --header-border-color: rgba(255, 255, 255, 0.1);

    .main-header-container .header-link-icon:hover {
      background-color: rgba(255, 255, 255, 0.05);
    }
  }

  &[data-nav-layout="horizontal"] {
    @media (min-width: 992px) {
      &[data-theme-mode="light"] {
        .header-element .horizontal-logo .header-logo {
          .desktop-dark,
          .toggle-logo,
          .toggle-dark,
          .desktop-white,
          .toggle-white {
            display: none;
          }

          .desktop-logo {
            display: block;
          }
        }
      }

      &[data-theme-mode="dark"] {
        .header-element .horizontal-logo .header-logo {
          .desktop-dark {
            display: block;
          }

          .desktop-logo,
          .toggle-logo,
          .toggle-dark,
          .desktop-white,
          .toggle-white {
            display: none;
          }
        }
      }
    }
  }
}

//header-gradient start

