	  margin-block-end: 22px;
	}
  }
  .timeline-image{
	border-radius:50%;
    width: 45px;
  }

  .timeline-body {
    img {
        max-width: 100%;
    }
  }

  @media (max-width: 576px){
	  .vtimeline .timeline-wrapper .timeline-panel {
		  width: auto !important;
		  margin-inline-start: 24% !important;
	  }
}

[data-theme-mode="dark"] {
	.timeline-panel {
		background-color: $white-2 !important;
	}
}