/* Timeline */
/*--- Vertical --*/
.vtimeline {
  list-style: none;
  padding: 0;
  position: relative;
  margin-block-end: 20px;
}
.vtimeline:before {
  inset-block-start: 0;
  inset-block-end: 0;
  position: absolute;
  content: " ";
  width: 2px;
  background-color: var(--default-border);
  inset-inline-start: 50%;
  margin-inline-start: -1.5px;
}
.vtimeline .timeline-wrapper {
  display: block;
  margin-block-end: 20px;
  position: relative;
  width: 100%;
  padding-inline-end: 90px;
}
.vtimeline .timeline-wrapper:before {
  content: " ";
  display: table;
}
.vtimeline .timeline-wrapper:after {
  content: " ";
  display: table;
  clear: both;
}
.vtimeline .timeline-wrapper .timeline-panel {
  padding: 20px;
  position: relative;
  background: var(--gray-1);
  border-radius: 5px;
  box-shadow: none;
  width: 35%;
  margin-inline-start: 15%;
}
.vtimeline .timeline-wrapper .timeline-panel:before {
  position: absolute;
  inset-block-start: 0;
  width: 100%;
  height: 2px;
  content: "";
  inset-inline-start: 0;
  inset-inline-end: 0;
}
.vtimeline .timeline-wrapper .timeline-panel:after {
  position: absolute;
  inset-block-start: 25px;
  inset-inline-end: -14px;
  display: inline-block;
  border-block-start: 14px solid transparent;
  border-inline-start: 14px solid var(--default-border);
  border-inline-end: 0 solid var(--default-border);
  border-block-end: 14px solid transparent;
  content: " ";
}
.vtimeline .timeline-wrapper .timeline-panel .timeline-title {
  font-size: 1rem;
  font-weight: 700;
  margin: 0 0 0.625rem;
}
.vtimeline .timeline-wrapper .timeline-panel .timeline-body p + p {
  margin-block-start: 5px;
}

.vtimeline .timeline-wrapper .timeline-panel .timeline-body ul {
  margin-block-end: 0;
}
.vtimeline .timeline-wrapper .timeline-panel .timeline-footer span {
  font-size: 0.6875rem;
}
.vtimeline .timeline-wrapper .timeline-badge {
  width: 45px;
  height: 45px;
  position: absolute;
  inset-block-start: 16px;
  inset-inline-start: calc(50% - 24px);
  z-index: 0;
  border-start-end-radius: 50%;
  border-start-start-radius: 50%;
  border-end-end-radius: 50%;
  border-end-start-radius: 50%;
  border: 0px solid #fff;
  border-radius: 50%;
}
.vtimeline .timeline-wrapper .timeline-badge i {
  color: #fff;
  text-align: center;
  font-size: 20px;
  line-height: 1.8;
  display: flex;
  margin: 0 auto;
  justify-content: center;
  inset-inline-start: 0;
  inset-inline-end: 0;
  padding: 1px 9px;
}
.vtimeline .timeline-wrapper.timeline-inverted {
  padding-inline-end: 0;
  padding-inline-start: 90px;
}
.vtimeline .timeline-wrapper.timeline-inverted .timeline-panel {
  margin-inline-start: auto;
  margin-inline-end: 15%;
}
.vtimeline .timeline-wrapper.timeline-inverted .timeline-panel:after {
  border-inline-start-width: 0;
  border-inline-end-width: 14px;
  inset-inline-start: -14px;
  inset-inline-end: auto;
}

.timeline-wrapper-primary .timeline-panel:before, .timeline-wrapper-primary .timeline-badge {
  background: var(--primary-color);
}

.timeline-wrapper-secondary .timeline-panel:before, .timeline-wrapper-secondary .timeline-badge {
